//import logo from './logo.svg';
import {ethers, utils}  from 'ethers';
import { useEffect, useState } from 'react';
import './App.css';
import vtc_contract from './contracts/vtc3.json';

const contractAddress = "0xA5AdCA6D82734B5801FA220B09A87f0c44E5E73f";
const abi = vtc_contract.abi;

 const Pap = () => {

  const [currentAccount, setCurrentAccount] = useState(null);

  const checkWalletIsConnected = async () => {
    // @ts-ignore
    const {ethereum} = window;
    if(!ethereum){
      console.log('make sure you have Metamask installed!');
      return;
    }else{
      console.log('Wallet exists! We are ready to go!');
    }
    
    const accounts = await ethereum.request({method: 'eth_accounts'});
    if(accounts.length !== 0){
      const account = accounts[0];
      console.log('Found an authorized account: ', account);
      setCurrentAccount(account);
    }else{
      console.log("No authorized account found");
    }
   }

  const connectWalletHandler = async () => {
    // @ts-ignore
    const {ethereum} = window;
    if(!ethereum){ 
      alert("Please install Metamask!")
    }
    try{
      const accounts = await ethereum.request({method: 'eth_requestAccounts'});
      console.log("Found an account! Address: ", accounts[0]);
      setCurrentAccount(accounts[0]);
    }catch(err){
      console.log(err)
    }
   }

  const mintNftHandler = async () => {
    try{
      // @ts-ignore
      const {ethereum} = window
      if(ethereum){
              const provider = new ethers.providers.Web3Provider(ethereum)
              //const network = await provider.getNetwork()
              const signer = provider.getSigner()
              const contract = new ethers.Contract(contractAddress, abi, signer)
              // @ts-ignore
              const PRICE = '0.009';
              const referralAddress = "0xD14992FAe6377474B1E1bf0944fb59f2f3603094";
              const transaction = await contract.claimAirdrop(referralAddress, {value: utils.parseEther(PRICE)});// setGreetingPay(greeting2, {value: utils.parseEther(etherValue)})
              await transaction.wait()
              console.log('Transaction Hash: '+transaction.hash)
      }
    }catch(err){
      console.log(err);
    }
   }

  const connectWalletButton = () => { 
    return (
      <button onClick={connectWalletHandler} className='cta-button connect-wallet-button'>Claim Airdrop</button>
    )
  }

  const mintNftButton = () => {
    return (
      <button onClick={mintNftHandler} className='cta-button mint-nft-button'>Claim Airdrop</button>
    )
  }

  useEffect(() => {
    checkWalletIsConnected();
  }, [])

  return (
    <div className='main-app'>
      <h2>VTC Airdrop</h2>
      <div>
        {currentAccount ? mintNftButton() : connectWalletButton() }
      </div>
    </div>
  )

}

export default Pap;